<template>
    <el-dialog :title="titleShow==true?'添加版本':'编辑版本'" :visible.sync="dialogForm" class="updatePwd">
        <el-form :model="form">
            <el-form-item label="" :label-width="formLabelWidth">
                <el-radio class="radio"  v-model="form.platform" label="1">安卓</el-radio>
            </el-form-item>
            <el-form-item label="渠道:" :label-width="formLabelWidth">
                <el-input v-model="form.dtu" auto-complete="off" placeholder="例如001"></el-input>
            </el-form-item>
            <el-form-item label="版本:" :label-width="formLabelWidth">
                <el-input v-model="form.version" auto-complete="off" placeholder="10101"></el-input>
            </el-form-item>
            <el-form-item label="小版本号:" :label-width="formLabelWidth">
                <el-input v-model="form.version_small" auto-complete="off"></el-input>
            </el-form-item>
            <el-form-item label="说明:" :label-width="formLabelWidth">
                <el-input v-model="form.remark" auto-complete="off"></el-input>
            </el-form-item>
            <el-form-item label="包大小:" :label-width="formLabelWidth">
                <el-input v-model="form.size" auto-complete="off"></el-input>
            </el-form-item>
            <el-form-item label="下载地址:" :label-width="formLabelWidth">
                <el-input v-model="form.url" auto-complete="off" placeholder="可不填"></el-input>
            </el-form-item>
            <el-form-item label="上传:" :label-width="formLabelWidth" v-if="form.platform==1">
                <el-upload
                        class="upload-demo"
                        ref="upload"
                        :action="url"
                        :on-preview="handlePreview"
                        :on-remove="handleRemove"
                        :file-list="fileList"
                        :name="file"
                        :data ='form'
                        :on-error ='handleError'
                        :on-success ='handleSucess'
                        :auto-upload="false">
                    <el-button slot="trigger" size="small" type="primary">选取文件</el-button>
                </el-upload>
            </el-form-item>
        </el-form>
        <div slot="footer" class="dialog-footer">
            <el-button @click="dialogForm = false">取 消</el-button>
            <el-button type="primary" @click="submitUpload">提交</el-button>
        </div>
    </el-dialog>
</template>

<style scope="" lang="less" type="text/scss">
    .updatePwd{
        .el-dialog__header{
            padding:30px;
            .el-dialog__headerbtn{
                width:50px;
                height:50px;
                margin:-30px -30px 0 0;
            }
        }
        .el-dialog__body{
            padding:30px 50px 0 0;
        }
    }
</style>

<script>
    import QuRequest from '../../core/QuRequest'
    import Auth from '../../core/Auth'

    const quRequest = new QuRequest()

    export default {

        data() {
            return {
                titleShow:false,
                dialogForm: false,
                formLabelWidth: '200px',
                disabled:true,
                isShow:true,
                sourceList:[],
                fileList:[],
                file:'file',
                url:quRequest.getUrl(quRequest.Apis.qutuiba.package.addPkg),
                form:{
                    remark: '',
                    version_small:'',
                    dtu:'',
                    platform:'1',
                    version:'',
                    size:'',
                    token:  Auth.getToken()
                }
            }
        },
        mounted() {
        },
        methods: {
            submitUpload(response) {
                this.$refs.upload.submit();
            },
            handleRemove(file, fileList) {
                console.log(file, fileList);
            },
            handlePreview(file) {
                console.log(file);
            },
            handleError(response){
                console.log('error'+response)
            },
            handleSucess(response, file, fileList){
                if(response.code!=0){
                    this.$message.error(response.message);
                }else{
                    this.$message.success('上传成功');
                    this.dialogForm = false;
                    this.$emit('update')
                }

               // this.imgUrl = response.data.url;
            },
            showInfoDialog() {
                this.fileList =[];
                this.titleShow = true;
                this.form.platform= '1';
                this.form.dtu= '';
                this.form.version_small ='',
                this.form.version='';
                this.form.remark ='';
                this.form.size = '';
                this.dialogForm = true;
            },
        }
    }
</script>


